
.header {
  height: 160px;
  margin-bottom: 60px;
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
  position: relative;
  z-index: 20;
}
.header__content {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  background-color: #1abc9c;
  transition: top 1ms 0.5s;
}
.header__content:after {
  position: absolute;
  bottom: 165px;
  transition: bottom 0.5ms;
  left: 50%;
  transform: translate(-50%, 50%);
  content: attr(data-author);
  color: rgba(255,255,255,0.4);
  font-size: 30px;
  font-family: fantasy;
}
.app__content--Active .header__content {
  transition: auto;
}
.header__menu {
  max-width: 1000px;
  background-color: #fff;
  margin: 90px auto 0;
  height: 120px;
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
  position: relative;
  z-index: 1;
}
.header__link {
  width: 80px;
  height: 80px;
}
.header__listSwitch {
  position: absolute;
  left: 10px;
  top: 10px;
  height: 25px;
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
}
.header__switch {
  z-index: 10;
}
.header--scroll {
  height: 50px;
  margin-bottom: 20px;
}
.header--scroll .header__content:after {
  bottom: 95px;
}
.header--scroll .header__menu {
  margin-top: 0;
  max-width: 100%;
  height: 50px;
  background-color: transparent;
}
.header--scroll .header__link {
  color: rgba(255,255,255,0.6);
  width: 50px;
  height: 50px;
}
.header--scroll .u-link--Active {
  color: #fff;
}
.header--scroll .header__listSwitch {
  top: 85vh;
  left: 5vw;
  background-color: #1abc9c;
  padding: 10px;
  border-radius: 50%;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.3);
}


