.slideFade-enter-active,
  .slideFade-leave-active {
    left: 10px;
}
}
.slideFade-enter-active,
.slideFade-leave-active {
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
}
.slideFade-enter {
  transform: translateX(100%) scale(0.3);
  opacity: 0;
}
.slideFade-leave-active {
  transform: translateX(-100%) scale(0.3);
  opacity: 0;
}


